html {
  font-size: 100%;
  font-family: "Rubik", sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
}

ol,
ul {
  list-style: none;
}

img {
  pointer-events: none;
  user-select: none;
}

main {
  min-height: 100vh;
  overflow: hidden;
  background-color: #FAFAFA;
  display: flex;
  position: relative;
}
@media (min-width: 46.25rem) {
  main {
    min-height: 50rem;
  }
}
main::before {
  content: "";
  position: absolute;
  z-index: 1;
  background-color: #973BFE;
}
@media (max-width: 46.1875em) {
  main::before {
    border-radius: 0 0 250px 0;
    width: 23.4375rem;
    height: 31.75rem;
    left: -50%;
    top: 0;
  }
}
@media (min-width: 46.25rem) {
  main::before {
    border-radius: 0 0 260px 242px;
    width: 34.7222222222vw;
    height: 48.6111111111vw;
    max-width: 31.25rem;
    max-height: 43.75rem;
    left: -5.83333333%;
    top: 0;
  }
}
main::after {
  content: "";
  position: absolute;
  z-index: 1;
  background-color: #F9F4FA;
}
@media (max-width: 46.1875em) {
  main::after {
    border-radius: 250px 0 0 0;
    width: 23.4375rem;
    height: 31.75rem;
    right: -50%;
    bottom: 0;
  }
}
@media (min-width: 46.25rem) {
  main::after {
    border-radius: 260px 242px 0 0;
    width: 34.7222222222vw;
    height: 48.6111111111vw;
    max-width: 31.25rem;
    max-height: 43.75rem;
    background-color: #423358;
    bottom: 0;
    right: -5.83333333%;
  }
}

.container {
  position: relative;
  z-index: 10;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 46.1875em) {
  .container {
    width: 65.86%;
    max-width: 484px;
    padding: 4rem 0;
    flex-direction: column;
    gap: 70px;
  }
}
@media (min-width: 46.25rem) {
  .container {
    width: 66%;
    max-width: 45.3125rem;
    gap: clamp(24px, 1.5rem + (1vw - 7.68px) * 14.881, 124px);
  }
}

.phone {
  background-color: #FFFFFF;
  padding: 0.625rem;
  box-shadow: 0 0 12px rgba(0, 0, 0, 0.4);
  width: 15.375rem;
  height: 31.625rem;
  border-radius: 28px;
}

.notch {
  background-image: linear-gradient(to left, #e942ff, #8838ff);
  border-radius: 18px 18px 6px 6px;
  height: 4.125rem;
}
.notch__camera {
  background-color: #FFFFFF;
  border-radius: 0 0 12px 12px;
  height: 1.125rem;
  width: 8.125rem;
  margin: 0 auto;
}
.notch__screen {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 3rem;
  padding: 0 0.625rem;
  position: relative;
}
.notch__back {
  color: #FFFFFF;
  cursor: pointer;
}
.notch__avatar {
  box-shadow: 0 0 0 2px #FFFFFF;
  border-radius: 50%;
  width: 21px;
  margin: 0 8px 0 6px;
}
.notch__name p {
  color: #FFFFFF;
  font-size: 12px;
  font-weight: 500;
  line-height: 0.4;
  margin-top: 6px;
}
.notch__name small {
  color: #CE79FF;
  font-size: 10px;
}
.notch__more {
  position: absolute;
  color: #FFFFFF;
  right: 16px;
  cursor: pointer;
}

.screen {
  background-color: #f5f3f8;
  border-radius: 0 0 22px 22px;
  padding: 0.625rem;
  height: 26.125rem;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

.message {
  border-radius: 8px;
  padding: 8px;
  font-size: 8px;
  max-width: 126px;
  word-wrap: break-word;
}
.message:not(:last-child) {
  margin-bottom: 8px;
}
.message--sent {
  align-self: flex-end;
}
.message--sent--text {
  background-color: #fff;
  color: #000;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}
.message--sent--multyImage {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.message--sent--multyImage img {
  width: 2.5rem;
  border-radius: 8px;
}
.message--received {
  align-self: flex-start;
}
.message--received--text {
  background-color: #ece6f2;
  color: #8D70A0;
}
.message--received--check {
  background-image: linear-gradient(to right, #e942ff, #8838ff);
  max-width: unset;
  width: 10rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.message--received--check .option {
  display: flex;
  align-items: center;
}
.message--received--check .option ion-icon {
  color: #ED63FF;
  margin-right: 8px;
  font-size: 0.75rem;
}
.message--received--check .option p {
  color: #fff;
  font-size: 0.5rem;
}
.message--received--check .price {
  color: #fff;
  font-weight: 500;
  font-size: 0.75rem;
}

.input {
  height: 2.25rem;
  border-radius: 40px;
  background-color: #fff;
  margin-top: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.3125rem;
}
.input input {
  padding-left: 17px;
  border: none;
  outline: none;
  max-width: 80%;
}
.input input::placeholder {
  color: #DADADA;
  font-size: 12px;
}
.input ion-icon {
  color: #412854;
  cursor: pointer;
  font-size: 24px;
}

@media (min-width: 46.25rem) {
  .simple-booking {
    width: 450px;
  }
}
.simple-booking h1 {
  color: #3c2652;
  margin-bottom: 1.875rem;
}
@media (max-width: 46.1875em) {
  .simple-booking h1 {
    text-align: center;
  }
}
.simple-booking p {
  color: #bbb6bd;
  font-size: 13px;
  line-height: 1.6;
}
@media (max-width: 46.1875em) {
  .simple-booking p {
    text-align: center;
  }
}